/* social media main page */
.background .content ul {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30vh;
  margin: 0;
  padding: 0;
}
.background .content ul li {
  align-items: center;
  position: relative;
  list-style: none;
  margin: 0 20px;
  cursor: pointer;
  
}
.background .content ul li a {
  text-decoration: none;
}
.background .content ul li a .fa-brands {
  font-size: 3em;
  color: #222;
}
.background .content ul li a::before {
  font-family: "FontAwesome";
  position: absolute;
  top: -30px;
  left: 8px;
  font-size: 3.3em;
  height: 0;
  
  overflow: hidden;
  transition: 0.5s ease-in-out;
  
}



.background .content ul li:nth-child(1) a::before {
  content: "\F16D";
  background-image: linear-gradient(45deg, 
    #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,
    #bc1888 100%);
  -webkit-background-clip: text;
  background-clip: text;
   -webkit-text-fill-color: transparent;
  border-bottom: 4px solid #dc2743;
}
.background .content ul li:nth-child(2) a::before {
  content: "\F232";
  color: #25D366;
  border-bottom: 4px solid #25D366;
}
.background .content ul li:nth-child(3) a::before {
  content: "\F099";
  color: #1DA1F2;
  border-bottom: 4px solid #1DA1F2;
}
.background .content ul li:hover a::before {
  height: 125%;
}
